import React from 'react';
import Link from 'next/link';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink } from 'reactstrap';

  const BtNavLink = (props) => {
      const {route , title} = props;

      return (
        <Link href={route}>
            <a className="nav-link">{title}</a>
        </Link>
      )
  }
export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar color="light" light expand="md">
          <NavbarBrand href="/">Abdi Noor</NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <BtNavLink route="/" title="Home" />
              </NavItem>
              <NavItem>
                <BtNavLink route="/about" title="About" />
              </NavItem>
              <NavItem>
                <BtNavLink route="/portfolio" title="portfolio" />
              </NavItem>
              <NavItem>
                <BtNavLink route="/blogs" title="blogs" />
              </NavItem>
              <NavItem>
                <BtNavLink route="/resume" title="resume" />
              </NavItem>
              
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}